/**
 * SSM Configuration Loader for BG-Remover Service
 *
 * Loads configuration and secrets from AWS SSM Parameter Store
 * following CarouselLabs multi-tenant patterns.
 */

import { SSMClient, GetParameterCommand } from '@aws-sdk/client-ssm';
import type { ServiceConfig, ServiceSecrets } from '../types';

const ssmClient = new SSMClient({
  region: process.env.AWS_REGION || 'eu-west-1',
});

// Cache for configuration
let configCache: ServiceConfig | null = null;
let secretsCache: ServiceSecrets | null = null;
let cacheExpiry = 0;
const CACHE_TTL_MS = 5 * 60 * 1000; // 5 minutes

/**
 * Get SSM parameter value
 */
async function getParameter(name: string, decrypt = false): Promise<string | null> {
  try {
    const response = await ssmClient.send(
      new GetParameterCommand({
        Name: name,
        WithDecryption: decrypt,
      })
    );
    return response.Parameter?.Value || null;
  } catch (error) {
    console.error(`Failed to get SSM parameter ${name}:`, error);
    return null;
  }
}

/**
 * Load service configuration from SSM
 */
export async function loadConfig(
  tenant: string = 'carousel-labs',
  stage: string = process.env.STAGE || 'dev'
): Promise<ServiceConfig> {
  // Return cached config if still valid
  if (configCache && Date.now() < cacheExpiry) {
    return configCache;
  }

  const configPath = `/tf/${stage}/${tenant}/services/bg-remover/config`;
  const configJson = await getParameter(configPath);

  if (configJson) {
    try {
      configCache = JSON.parse(configJson) as ServiceConfig;
      cacheExpiry = Date.now() + CACHE_TTL_MS;
      return configCache;
    } catch (error) {
      console.error('Failed to parse config JSON:', error);
    }
  }

  // Return default configuration if SSM parameter not found
  const defaultConfig: ServiceConfig = {
    bedrock: {
      modelId: 'anthropic.claude-3-5-sonnet-20241022-v2:0',
      region: 'us-east-1', // Bedrock region
      maxTokens: 4096,
    },
    s3: {
      inputBucket: `smartgo-s3tables-${stage}-${tenant}`,
      outputBucket: `bg-remover-${stage}-${tenant}-output`,
      region: 'eu-west-1',
    },
    processing: {
      maxImageSizeMb: 10,
      supportedFormats: ['jpg', 'jpeg', 'png', 'webp', 'heic'],
      defaultQuality: 95,
    },
  };

  configCache = defaultConfig;
  cacheExpiry = Date.now() + CACHE_TTL_MS;
  return defaultConfig;
}

/**
 * Load service secrets from SSM
 */
export async function loadSecrets(
  tenant: string = 'carousel-labs',
  stage: string = process.env.STAGE || 'dev'
): Promise<ServiceSecrets> {
  // Return cached secrets if still valid
  if (secretsCache && Date.now() < cacheExpiry) {
    return secretsCache;
  }

  const secretsPath = `/tf/${stage}/${tenant}/services/bg-remover/secrets`;
  const secretsJson = await getParameter(secretsPath, true);

  if (secretsJson) {
    try {
      secretsCache = JSON.parse(secretsJson) as ServiceSecrets;
      return secretsCache;
    } catch (error) {
      console.error('Failed to parse secrets JSON:', error);
    }
  }

  // Return empty secrets if not found
  secretsCache = {};
  return secretsCache;
}

/**
 * Load both config and secrets
 */
export async function loadConfigAndSecrets(
  tenant: string = 'carousel-labs',
  stage: string = process.env.STAGE || 'dev'
): Promise<{ config: ServiceConfig; secrets: ServiceSecrets }> {
  const [config, secrets] = await Promise.all([
    loadConfig(tenant, stage),
    loadSecrets(tenant, stage),
  ]);

  return { config, secrets };
}

/**
 * Clear configuration cache (useful for testing)
 */
export function clearCache(): void {
  configCache = null;
  secretsCache = null;
  cacheExpiry = 0;
}
